<template>
  <div class="uploader">
    <h1>File Uploader (Offline-first)</h1>

    <p>
      Status: <strong>{{ isOnline ? "Online" : "Offline" }}</strong>
    </p>

    <input type="file" multiple @change="onPick" />

    <button @click="syncNow" :disabled="syncing">Sync now</button>

    <h2>Queue</h2>
    <table width="100%">
      <tr v-for="item in queue" :key="item.id">
        <td width="60%">
          <!-- Image Preview -->
          <div v-if="isImage(item)" class="preview">
            <img class="h-auto max-w-full p-4" :src="fileURL(item)" :alt="item.name" />
          </div>

          <!-- PDF Preview -->
          <div v-else-if="isPDF(item)" class="preview">
            <iframe :src="fileURL(item)" frameborder="0" class="w-full h-64"></iframe>
          </div>

          <!-- Video Preview -->
          <div v-else-if="isVideo(item)" class="preview">
            <video class="w-full h-64" controls>
              <source :src="fileURL(item)" :type="item.type" />
              Your browser does not support the video tag.
            </video>
          </div>

          <!-- Audio Preview -->
          <div v-else-if="isAudio(item)" class="preview">
            <audio controls>
              <source :src="fileURL(item)" :type="item.type" />
              Your browser does not support the audio tag.
            </audio>
          </div>

          <!-- Text Preview -->
          <div v-else-if="isText(item)" class="preview">
            <iframe :src="fileURL(item)" frameborder="0" class="w-full h-64"></iframe>
          </div>

          <!-- ZIP / Archive Preview -->
          <div v-else-if="isArchive(item)" class="preview text-center p-4">
            <div class="p-4 border rounded bg-gray-100">
              <p class="font-bold">{{ item.name }}</p>
              <p class="text-sm text-gray-600">{{ item.type || "Archive File" }}</p>
              <a
                :href="fileURL(item)"
                download
                class="mt-2 inline-block bg-blue-500 text-white px-3 py-1 rounded"
              >
                Download ZIP
              </a>
            </div>
          </div>

          <!-- Fallback: Other file types -->
          <div v-else class="preview">
            <a :href="fileURL(item)" target="_blank" class="text-blue-500 underline">
              {{ item.name }} ({{ item.type || "Unknown type" }})
            </a>
          </div>
        </td>

        <td width="20%">
          <h2>{{ item.name }} ({{ human(item.size) }})</h2>
        </td>
        <td>
          <button @click="rename(item)">Rename</button>
          <button @click="replace(item)">Replace</button>
          <button @click="deleteItem(item)">Delete</button>
          <span v-if="item.uploading"> — uploading... </span>
        </td>
      </tr>
    </table>

    <h2>Uploaded</h2>
    <table>
      <tr v-for="u in uploaded" :key="u.ts">
        <td>{{ u.name }}</td>
      </tr>
    </table>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { Asset } from "@/entities/asset.entity";
import { assetLocalService } from "@/service/asset.local.service.js";

const isOnline = ref(navigator.onLine);
const queue = ref([]);
const uploaded = ref([]);
const syncing = ref(false);

function human(bytes) {
  const units = ["B", "KB", "MB", "GB"];
  let i = 0,
    n = bytes;
  while (n >= 1024 && i < units.length - 1) {
    n /= 1024;
    i++;
  }
  return `${n.toFixed(1)} ${units[i]}`;
}

function isImage(f) {
  return f.type.startsWith("image/");
}

function isPDF(f) {
  return f.type === "application/pdf";
}

function isVideo(f) {
  return f.type.startsWith("video/");
}

function isAudio(f) {
  return f.type.startsWith("audio/");
}

function isText(f) {
  return f.type.startsWith("text/");
}

// ZIP, RAR, 7z and other archive types
function isArchive(f) {
  return (
    f.type === "application/zip" ||
    f.type === "application/x-zip-compressed" ||
    f.type === "application/x-rar-compressed" ||
    f.type === "application/x-7z-compressed" ||
    f.name?.toLowerCase().endsWith(".zip") ||
    f.name?.toLowerCase().endsWith(".rar") ||
    f.name?.toLowerCase().endsWith(".7z")
  );
}

function fileURL(f) {
  return URL.createObjectURL(f.file);
}

async function refreshQueue() {
  const res = await assetLocalService.getAll(1, 500);
  queue.value = res.data;
}

async function onPick(e) {
  let new_data = new Asset();
  const files = Array.from(e.target.files || []);
  for (const f of files) {
    const payload = {
      name: f.name,
      size: f.size,
      type: f.type,
      file: f,
      status: "pending",
    };
    new_data = { ...new_data, ...payload };
    await assetLocalService.create(new_data);
  }
  await refreshQueue();
}

// CRUD operations
async function rename(item) {
  const newName = prompt("Enter new name:", item.name);
  if (!newName) return;
  item.name = newName;
  item.status = "pending";
  await assetLocalService.update(item.uuid, item);
}

async function replace(item) {
  const input = document.createElement("input");
  input.type = "file";
  input.onchange = async (e) => {
    const f = e.target.files[0];
    if (!f) return;
    item.name = f.name;
    item.size = f.size;
    item.type = f.type;
    item.file = f;
    item.status = "pending";
    await assetLocalService.update(item.uuid, item);
  };
  input.click();
}

async function deleteItem(item) {
  if (!confirm(`Delete ${item.name}?`)) return;
  await assetLocalService.remove(item.uuid);
  await refreshQueue();
}

async function syncNow() {
  syncing.value = true;
  //   try { triggerSync() }
  //   finally { setTimeout(async () => { await refreshQueue(); syncing.value = false }, 500) }
}

// function handleOnline() { isOnline.value = true; triggerSync() }
// function handleOffline() { isOnline.value = false }

onMounted(async () => {
  await refreshQueue();
});

onBeforeUnmount(() => {
  //   window.removeEventListener('online', handleOnline)
  //   window.removeEventListener('offline', handleOffline)
});
</script>

<style scoped>
h1 {
  margin-bottom: 0.5rem;
}

.preview {
  width: 250px;
  height: 250px;
  overflow: hidden;
  margin-right: 0.5rem;
  display: inline-block;
  vertical-align: middle;
}

.preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.preview iframe {
  width: 250px;
  height: 250px;
}

button {
  margin-left: 0.3rem;
}
</style>
