// src/services/fileUploadLocalService.js
import { LocalDB } from '@/db';
import { Asset } from '@/entities/asset.entity';
import { getJWTUser } from '@/utils/jwt';

import { getSQLRepository } from '@mamunmo21/idxlite';

export const assetLocalService = {
  async getAll(page = 1, limit = 10) {
    try {
      const user = await getJWTUser();
      const db = await LocalDB();
      const assetRepo = getSQLRepository(db, Asset);
      const [data, total] = await assetRepo
        .createQueryBuilder('p')
        // .where({ is_deleted: false, user_uuid: user.uuid })
        .skip((page - 1) * limit)
        .take(limit)
        .orderBy({ created_at: "DESC" })
        .findAndCount();
      return {
        data,
        total,
        page: Number(page),
        last_page: Math.ceil(total / limit),
      };
    } catch (err) {
      console.error('Failed to fetch assetRepo:', err);
      return { success: false, message: err.message };
    }
  },

  async create(payload) {
    try {
      const db = await LocalDB();
      const assetRepo = getSQLRepository(db, Asset);
      if (!payload.created_at) {
        payload.created_at = new Date().toISOString();
      }
      if (!payload.updated_at) {
        payload.updated_at = new Date().toISOString();
      }

      const saved = await assetRepo.save(payload);
      return { success: true, data: saved };
    } catch (err) {
      console.log('err: ', err);
      return { success: false, message: err.message };
    }
  },

  async update(uuid, payload) {
    try {
      const db = await LocalDB();
      const assetRepo = getSQLRepository(db, Asset);
      const cat = await assetRepo
        .createQueryBuilder("p")
        .where({ uuid: uuid })
        .getOne();
      if (!cat) return { success: false, message: 'Not found' };
      Object.assign(cat, payload);
      await assetRepo.update(cat.id, cat);
      const updatedOne = await assetRepo
        .createQueryBuilder("p")
        .where({ uuid: uuid })
        .getOne();
      return { success: true, data: updatedOne };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },

  async remove(uuid) {
    try {
      const db = await LocalDB();
      const assetRepo = getSQLRepository(db, Asset);
      const cat = await assetRepo
        .createQueryBuilder("p")
        .where({ uuid: uuid })
        .getOne();
      if (!cat) return { success: false, message: 'Not found' };
      await assetRepo.delete(cat.id);
      return { success: true, message: 'Deleted' };
    } catch (err) {
      return { success: false, message: err.message };
    }
  },

  async processQueueSave(payload) {
    try {
      const db = await LocalDB();
      const assetRepo = getSQLRepository(db, Asset);
      payload.id = Number(payload.id); // Ensure id is a number
      const existing = await assetRepo.findOne(payload.id);
      if (existing) {
        await assetRepo.update(payload.id, payload);
      } else {
        await assetRepo.save(payload);
      }
    } catch (error) {
      console.error('Error syncing categories:', error);
    }
  }
};
